* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
  height: 100%;
}

.container {
    min-height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container-buttons-menu{
    display:flex;
    flex-direction: row;
}
.container-buttons-menu button{
    width:33.3vw;
    border:none;
    outline: none;
    border-right: 1px black solid;
    border-bottom: 1px black solid;
    padding: 10px;
    cursor: pointer;
    background: #E0DCE4;
}
.container-buttons-menu button:hover{
    border-bottom: 1px #53BCAD solid;
}
.container-desenvolvimento {
    display:flex;
    justify-content: center
}

.emDesenvolvimento {
    padding:15px;
    border-radius:5px;
    border:none;
    outline: none;
    background: #2B77C0;
    color:white;
    font-size: 1em;
    font-weight: 500;
    cursor:pointer;
    position: absolute;
    bottom: -50px;
}
.emDesenvolvimento:hover{
    opacity: 0.9;
}

input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
.container-gramatica {
    display:flex;
    flex-direction: column;
    margin-top: 10%;
    font-size: 1.3em;
    font-weight: bold;
    align-items: center;    
}
.sentenca {
    border: none;
    padding: 10px;
    cursor: pointer;
}
.sentenca-container {
    border: 1px solid black;
    display: flex;
    margin:1px;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
}
.sentenca-container:hover {
    background:whitesmoke;
}
.sentenca-container:hover input{
    background:whitesmoke;
}
.sentenca-container input {
    font-size: 1.3em;
}
.container-buttons-menu-af{
    display:flex;
    width:100%;
    flex-wrap: wrap;
}


.container-buttons-menu-af button {
    display:flex;
    justify-content: center;
    border:1px black solid;
    border-right: 1px black solid;
    background-color: white;
    cursor: pointer;
    width:25%;
    flex-grow: 1;
    padding: 5px;
}
.container-buttons-menu-af * {
    font-size: 1.2em;
}

.container-buttons-menu-af *:hover {
    background: whitesmoke;
}

.table{
    border: 1px solid black;
    margin: 20px;
}
.headerTable {
    display: flex;
}
.cellTable {
    border: 1px solid black;
    width: 75px;
    height: 25px;
    text-align: center;
    cursor: pointer;
}
.containerBody {
    display: flex;
}
.lineTable {
    display:flex;   
}
.headerLeftTable {
    display: flex;
    flex-direction: column;
}
input[type='file'] {
    display: none;
}
label {
    display:flex;
    justify-content: center;
    border:1px black solid;
    border-right: 1px black solid;
    background-color: white;
    cursor: pointer;
    width:25%;
    flex-grow: 1;
    padding: 5px;
}
.container-inputs {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: left;
    width: 100%;
}
.container-inputs input {
    max-width: 400px;
    min-width: 300px;
}
.container-inputs p {
    margin-bottom: 20px;
    font-weight: bold;
    font-size: 2em;
}
